import React, { useState, useRef, useEffect } from 'react';
import { View, Text, StyleSheet, TouchableOpacity, ScrollView, SafeAreaView, Image, 
         Dimensions, Platform, Animated, Modal, TouchableWithoutFeedback } from 'react-native';
import { useNavigation, useFocusEffect } from '@react-navigation/native';
import ScrollableGradientWrapper from '../components/ScrollableGradientWrapper';
import Svg, { Path } from 'react-native-svg';

const { width: SCREEN_WIDTH } = Dimensions.get('window');
const MAX_COLUMN_WIDTH = 400;
const BUTTON_WIDTH = Math.min(320, SCREEN_WIDTH * 0.8);

// isWebDesktop function remains the same
export const isWebDesktop = () => {
  const isWeb = Platform.OS === 'web';
  try {
    const isLikelyDesktop = isWeb && Dimensions.get('window').width > 768;
    return isLikelyDesktop;
  } catch (error) {
    return false;
  }
};

// FittingTypeButton component
const FittingTypeButton = ({ type, description, onPress, isSelected, onHelpPress }) => (
  <TouchableOpacity 
    style={[styles.fittingButton, isSelected && styles.selectedFittingButton]} 
    onPress={onPress}
  >
    <View style={styles.fittingContent}>
      <Text style={styles.fittingButtonText}>{type}</Text>
      <Text style={styles.fittingDescription}>{description}</Text>
    </View>
    <TouchableOpacity 
      style={styles.helpIcon} 
      onPress={onHelpPress}
      activeOpacity={0.6}
      hitSlop={{ top: 10, bottom: 10, left: 10, right: 10 }}
    >
      <View style={styles.helpIconBackground}>
        <Text style={styles.helpIconText}>?</Text>
      </View>
    </TouchableOpacity>
  </TouchableOpacity>
);

const End1FittingScreen = ({ route }) => {
  const navigation = useNavigation();
  const [selectedFitting, setSelectedFitting] = useState(null);
  const { selectedHose, selectedHosePrice, isOrderFittingMode } = route.params || {};
  const [showSizeModal, setShowSizeModal] = useState(false);
  const [selectedSize, setSelectedSize] = useState(null);
  const sizeFadeAnim = useRef(new Animated.Value(0)).current;
  const fadeAnim = useRef(new Animated.Value(0)).current;
  const [showHelpOverlay, setShowHelpOverlay] = useState(false);
  const [currentHelpImage, setCurrentHelpImage] = useState(null);
  const [showDiagramOverlay, setShowDiagramOverlay] = useState(false);
  const helpModalFadeAnim = useRef(new Animated.Value(0)).current;
  const diagramModalFadeAnim = useRef(new Animated.Value(0)).current;

  const fittingTypes = [
    { type: 'BSP 60°', description: 'British Standard Pipe', helpImage: require('../assets/bsp_help.png') },
    { type: 'JIC 37°', description: 'Joint Industrial Council', helpImage: require('../assets/jic_help.png') },
    { type: 'METRIC LIGHT 24°', description: 'Cone O-Ring', helpImage: require('../assets/metric_help.png') },
    { type: 'O.R.F.S', description: 'O-Ring Flat Seal', helpImage: require('../assets/orfs_help.png') },
  ];

  const hoseSizes = ["1/4\"", "3/8\"", "1/2\"", "5/8\"", "3/4\"", "1\""];

  useEffect(() => {
    // Fade in animation when the component mounts
    Animated.timing(fadeAnim, {
      toValue: 1,
      duration: 500,
      useNativeDriver: Platform.OS !== 'web', // Fix: changed from 'native' to !== 'web'
    }).start();
  }, []);

  useFocusEffect(
    React.useCallback(() => {
      // Reset the selection and fade in when the screen comes into focus
      fadeAnim.setValue(0);
      Animated.timing(fadeAnim, {
        toValue: 1,
        duration: 500,
        useNativeDriver: Platform.OS !== 'web', // Fix: changed from 'native' to !== 'web'
      }).start();

      return () => {
        // Optional cleanup if needed
      };
    }, [fadeAnim])
  );

  // Define handleFittingSelection function
  const handleFittingSelection = (type) => {
    setSelectedFitting(type);
    
    if (isOrderFittingMode) {
      // Show size selection modal for OrderFitting mode
      setShowSizeModal(true);
      sizeFadeAnim.setValue(0);
      Animated.timing(sizeFadeAnim, {
        toValue: 1,
        duration: 800,
        useNativeDriver: Platform.OS !== 'web',
      }).start();
      return;
    }
    
    Animated.timing(fadeAnim, {
      toValue: 0,
      duration: 800,
      useNativeDriver: Platform.OS !== 'web',
    }).start(() => {
      const baseParams = {
        selectedFitting: { type },
        isOrderFittingMode,
        selectedHose,
        selectedHosePrice
      };

      // Switch statement uses baseParams directly
      switch (type) {
        case 'BSP 60°':
          navigation.navigate('BSPFittingScreen1', baseParams);
          break;
        case 'JIC 37°':
          navigation.navigate('JICFittingScreen1', baseParams);
          break;
        case 'METRIC LIGHT 24°':
          navigation.navigate('MetricFittingScreen1', baseParams);
          break;
        case 'O.R.F.S':
          navigation.navigate('OrfsScreen1', baseParams);
          break;
        default:
          console.log('Selected Hose:', selectedHose);
          console.log('Selected Fitting:', type);
      }
    });
  };

  // Define handleSizeSelection function
  const handleSizeSelection = (size) => {
    setSelectedSize(size);
    
    // Navigate to the appropriate screen with both fitting and size selection
    const baseParams = {
      selectedFitting: { type: selectedFitting },
      isOrderFittingMode: true,
      selectedHose: { size },
      selectedHosePrice: '0'
    };

    Animated.timing(sizeFadeAnim, {
      toValue: 0,
      duration: 500,
      useNativeDriver: Platform.OS !== 'web',
    }).start(() => {
      setShowSizeModal(false);
      switch (selectedFitting) {
        case 'BSP 60°':
          navigation.navigate('BSPFittingScreen1', baseParams);
          break;
        case 'JIC 37°':
          navigation.navigate('JICFittingScreen1', baseParams);
          break;
        case 'METRIC LIGHT 24°':
          navigation.navigate('MetricFittingScreen1', baseParams);
          break;
        case 'O.R.F.S':
          navigation.navigate('OrfsScreen1', baseParams);
          break;
      }
    });
  };

  // Define handleCloseSizeModal function
  const handleCloseSizeModal = () => {
    Animated.timing(sizeFadeAnim, {
      toValue: 0,
      duration: 500,
      useNativeDriver: Platform.OS !== 'web',
    }).start(() => {
      setShowSizeModal(false);
      setSelectedSize(null);
      setSelectedFitting(null);
    });
  };
  
  // FIX: Updated handleHelpPress function to properly handle the image
  const handleHelpPress = (helpImage) => {
    
    // Ensure we're using the correct image
    setCurrentHelpImage(helpImage);
    setShowHelpOverlay(true);
    helpModalFadeAnim.setValue(0);
    Animated.timing(helpModalFadeAnim, {
      toValue: 1,
      duration: 800,
      useNativeDriver: Platform.OS !== 'web',
    }).start();
  };

  const closeHelpOverlay = () => {
    Animated.timing(helpModalFadeAnim, {
      toValue: 0,
      duration: 500,
      useNativeDriver: Platform.OS !== 'web',
    }).start(() => {
      setShowHelpOverlay(false);
    });
  };

  const handleDiagramPress = () => {
    setShowDiagramOverlay(true);
    diagramModalFadeAnim.setValue(0);
    Animated.timing(diagramModalFadeAnim, {
      toValue: 1,
      duration: 800,
      useNativeDriver: Platform.OS !== 'web',
    }).start();
  };
    
  const closeDiagramOverlay = () => {
    Animated.timing(diagramModalFadeAnim, {
      toValue: 0,
      duration: 500,
      useNativeDriver: Platform.OS !== 'web',
    }).start(() => {
      setShowDiagramOverlay(false);
    });
  };

  return (
    <View style={styles.mainContainer}>
      {/* Floating Back Button - Outside scroll wrapper */}
      <TouchableOpacity 
        style={styles.floatingBackButton}
        onPress={() => navigation.goBack()}
      >
        <Svg width="24" height="24" viewBox="0 0 24 24" fill="none">
          <Path 
            d="M19 12H5M12 19L5 12L12 5" 
            stroke="#4a4a4a" 
            strokeWidth="2.5" 
            strokeLinecap="round" 
            strokeLinejoin="round"
          />
        </Svg>
      </TouchableOpacity>
  
      <ScrollableGradientWrapper>
        <SafeAreaView style={styles.container}>
          <View style={styles.logoContainer}>
            <Image
              source={require('../assets/fluidpower_logo.gif')}
              style={styles.logo}
              resizeMode="contain"
            />
          </View>
          
          <Animated.View style={[styles.contentContainer, { opacity: fadeAnim }]}>
            <View style={styles.titleContainer}>
              <View style={styles.titleWrapper}>
                {isOrderFittingMode ? (
                  <Text style={styles.titleText}>Select Thread Type</Text>
                ) : (
                  <>
                    <Text style={styles.titleText}>Select END </Text>
                    <View style={styles.circleWrapper}>
                      <Text style={styles.circleText}>1</Text>
                    </View>
                    <Text style={styles.titleText}> Thread</Text>
                  </>
                )}
              </View>
            </View>
  
            {/* Conditional rendering for diagram with spacing compensation */}
            {isOrderFittingMode ? (
              <View style={styles.diagramSpacing} />
            ) : (
              <>
                <TouchableOpacity onPress={handleDiagramPress}>
                  <Image
                    source={require('../assets/end1_thread_diagram.png')}
                    style={styles.diagram}
                    resizeMode="contain"
                  />
                  <Text style={styles.clickToEnlargeText}>Click photo to enlarge</Text>
                </TouchableOpacity>
              </>
            )}
            
            <ScrollView style={styles.scrollView} contentContainerStyle={styles.scrollViewContent}>
              {fittingTypes.map((fitting, index) => (
                <FittingTypeButton
                  key={index}
                  type={fitting.type}
                  description={fitting.description}
                  onPress={() => handleFittingSelection(fitting.type)}
                  isSelected={selectedFitting === fitting.type}
                  onHelpPress={() => handleHelpPress(fitting.helpImage)}
                />
              ))}
            </ScrollView>
          </Animated.View>
        </SafeAreaView>
      </ScrollableGradientWrapper>
  
      {/* FIX: Updated help modal to properly display the image */}
      <Modal
        transparent={true}
        visible={showHelpOverlay}
        onRequestClose={closeHelpOverlay}
        animationType="none" // Control animation manually
      >
        <View style={styles.modalOverlay}>
          <TouchableOpacity
            style={styles.overlayBackground}
            activeOpacity={1}
            onPress={closeHelpOverlay}
          />
          <Animated.View style={[
            styles.helpImageContainer,
            {
              opacity: helpModalFadeAnim,
              transform: [{ scale: helpModalFadeAnim }]
            }
          ]}>
            <TouchableOpacity
              style={styles.closeModalButton}
              onPress={closeHelpOverlay}
            >
              <Text style={styles.closeModalButtonText}>×</Text>
            </TouchableOpacity>
            <Text style={styles.modalTitle}>Fitting Information</Text>
            
            {/* FIX: Updated image rendering approach */}
            {currentHelpImage && (
              <View style={styles.imageWrapper}>
                <Image
                  source={currentHelpImage}
                  style={styles.helpImage}
                  resizeMode="contain"
                  onError={(e) => console.error("Image load error:", e.nativeEvent.error)}
                />
              </View>
            )}
          </Animated.View>
        </View>
      </Modal>

      {/* Diagram overlay modal */}
      <Modal
        transparent={true}
        visible={showDiagramOverlay}
        onRequestClose={closeDiagramOverlay}
        animationType="none"
      >
        <View style={styles.modalOverlay}>
          <TouchableOpacity
            style={styles.overlayBackground}
            activeOpacity={1}
            onPress={closeDiagramOverlay}
          />
          <Animated.View style={[
            styles.diagramImageContainer, 
            { 
              opacity: diagramModalFadeAnim, 
              transform: [{ scale: diagramModalFadeAnim }] 
            }
          ]}>
            <TouchableOpacity
              style={styles.closeModalButton}
              onPress={closeDiagramOverlay}
            >
              <Text style={styles.closeModalButtonText}>×</Text>
            </TouchableOpacity>
            <Text style={styles.modalTitle}>Thread Diagram</Text>
            <Image
              source={require('../assets/end1_thread_diagram.png')}
              style={styles.enlargedDiagram}
              resizeMode="contain"
            />
          </Animated.View>
        </View>
      </Modal>

      {/* Size selection modal */}
      <Modal
        transparent={true}
        visible={showSizeModal}
        onRequestClose={handleCloseSizeModal}
      >
        <View style={styles.modalOverlay}>
          <TouchableOpacity 
            style={styles.overlayBackground}
            activeOpacity={1}
            onPress={handleCloseSizeModal}
          />
          <Animated.View style={[
            styles.selectionContainer, 
            { 
              opacity: sizeFadeAnim, 
              transform: [{ scale: sizeFadeAnim }] 
            }
          ]}>
            <TouchableOpacity 
              style={styles.closeButton}
              onPress={handleCloseSizeModal}
            >
              <Text style={styles.closeButtonText}>×</Text>
            </TouchableOpacity>
            
            <Text style={styles.selectionTitle}>
              Select the Hose Size you're ordering the Fittings for:
            </Text>
            
            {hoseSizes.map((size, index) => (
              <TouchableOpacity
                key={index}
                style={[
                  styles.selectionButton,
                  selectedSize === size && styles.selectedButton
                ]}
                onPress={() => handleSizeSelection(size)}
              >
                <Text style={styles.selectionButtonText}>{size}</Text>
              </TouchableOpacity>
            ))}
          </Animated.View>
        </View>
      </Modal>
    </View>
  );
};

