// utils/emailService.js

export const sendOrderEmail = async (orderDetailsFromConfirmation) => { // Renamed param for clarity
  
  try {
    // Use environment variable for API URL (ensure it's set correctly, e.g., http://localhost:3000)
    // Make sure this evaluates correctly in your environment
    const API_BASE_URL = process.env.NEXT_PUBLIC_API_URL || process.env.REACT_APP_API_URL || 'http://localhost:3000'; // Add fallbacks
    const SERVER_KEY = process.env.NEXT_PUBLIC_SERVER_KEY || process.env.REACT_APP_SERVER_KEY; // Get server key

    if (!API_BASE_URL) {
        console.error("[emailService] API_BASE_URL is not defined.");
        throw new Error("API URL configuration error.");
    }
     if (!SERVER_KEY) {
        console.error("[emailService] SERVER_KEY is not defined.");
        throw new Error("Server key configuration error.");
    }

    // --- Recalculate Price Breakdown using the correct function ---
    // Ensure calculatePriceBreakdown is robust and uses nested params correctly
    const priceBreakdown = calculatePriceBreakdown(orderDetailsFromConfirmation); // Use the function below

    // --- Generate HTML Content ---
    // Ensure these functions use the correct priceBreakdown object
    const priceBreakdownHTML = generatePriceBreakdownHTML(priceBreakdown);
    const businessEmailContent = generateBusinessEmailContent(orderDetailsFromConfirmation, priceBreakdownHTML);
    const customerEmailContent = generateCustomerEmailContent(orderDetailsFromConfirmation, priceBreakdownHTML);

    // --- Prepare PDF Attachment ---
    let pdfBase64 = null; // Rename for clarity
    if (orderDetailsFromConfirmation.pdfAttachment) {
      try {
         // Assuming pdfAttachment is already a base64 string or ArrayBuffer/Buffer from PDFGenerator
         if (typeof orderDetailsFromConfirmation.pdfAttachment === 'string') {
             pdfBase64 = orderDetailsFromConfirmation.pdfAttachment; // Already base64
         } else if (orderDetailsFromConfirmation.pdfAttachment instanceof ArrayBuffer || Buffer.isBuffer(orderDetailsFromConfirmation.pdfAttachment)) {
             // Convert ArrayBuffer/Buffer to base64
             const buffer = Buffer.from(orderDetailsFromConfirmation.pdfAttachment);
             pdfBase64 = buffer.toString('base64');
         } else {
             console.warn("[emailService] pdfAttachment is in an unexpected format:", typeof orderDetailsFromConfirmation.pdfAttachment);
             // Handle other types if necessary, or default to null
         }
      } catch (pdfError) {
          console.error("[emailService] Error processing pdfAttachment:", pdfError);
          // pdfBase64 remains null
      }
    }

    // --- *** CORRECTLY Construct the payload for the backend *** ---
    const payloadToSend = {
      // Explicitly include fields expected by /api/send-email
      orderNumber: orderDetailsFromConfirmation.orderNumber || 'Unknown', // Get from top level
      userDetails: orderDetailsFromConfirmation.userDetails || {}, // Get from top level
      pdfAttachment: pdfBase64, // Use the processed base64 string
      emailTemplates: { // Pass the generated HTML content
        businessEmailContent: businessEmailContent,
        customerEmailContent: customerEmailContent
      },
      // Include userEmail as a fallback if needed by backend validation
      userEmail: orderDetailsFromConfirmation.userDetails?.email // Can add fallback if needed
    };

    // --- Send to server ---
    const response = await fetch(`${API_BASE_URL}/api/send-email`, {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
        // Use the correct server key environment variable
        'x-server-key': SERVER_KEY,
      },
      body: JSON.stringify(payloadToSend) // Send the correctly structured payload
    });

    // Use .text() first to see raw response if JSON parsing fails
    const responseText = await response.text();

    if (!response.ok) {
      // Try parsing as JSON for error message, otherwise use text
      let errorMsg = `Failed to send email (${response.status})`;
      try {
          const errorData = JSON.parse(responseText);
          errorMsg = errorData.error || errorMsg;
      } catch (parseError) {
          // Ignore if response wasn't JSON
      }
      throw new Error(errorMsg);
    }

    // Try parsing successful response as JSON
     let data = { success: true }; // Assume success if response.ok
     try {
         data = JSON.parse(responseText);
     } catch(parseError) {
         console.warn("[emailService] Backend response was OK but not valid JSON:", responseText);
     }
    return { success: true, data: data }; // Return success

  } catch (error) {
    console.error('[emailService] Error in sendOrderEmail:', error.message);
    return { success: false, error: error.message }; // Return failure
  }
};

// --- Helper Function: Calculate Price Breakdown ---
// Ensure this function correctly accesses nested params from the input object
const calculatePriceBreakdown = (details) => {
    // Default values
    let prices = { subtotal: 0, shipping: 0, gst: 0, total: 0 };

    try {
        // Check if the necessary nested structure exists
        if (!details || !details.originalOrderParams) {
            console.warn("[emailService > calculatePriceBreakdown] originalOrderParams missing in details:", details);
            return prices; // Return zeroed object
        }
        const params = details.originalOrderParams;
        const fittingMode = details.isOrderFittingMode ?? params.isOrderFittingMode ?? false;

        if (fittingMode) {
            const subtotal = parseFloat(params.cutLengthPrice) || 0;
            const shipping = 12.85; // Fixed shipping for Fitting Mode? Check this logic
            const subtotalWithShipping = subtotal + shipping;
            const gst = subtotalWithShipping * 0.1;
            const total = subtotalWithShipping + gst;
            prices = { subtotal, shipping, gst, total };
        } else { // Hose or Normal Mode
            const end1Cost = parseFloat(params.end1Price) || 0;
            const end2Cost = parseFloat(params.end2Price) || 0;
            let calculatedCutLengthPrice = 0;
            if (params.cutLengths && params.cutLengths.length > 0) {
                 const totalLength = params.cutLengths.reduce((total, cut) => {
                     if (!cut || !cut.length) return total;
                     return total + (parseFloat(cut.length) / 1000);
                 }, 0);
                 const hosePriceStr = typeof params.selectedHosePrice === 'string' ? params.selectedHosePrice : '0';
                 const hosePricePerMeter = parseFloat(hosePriceStr.replace(/\s+/g, '').split('/')[0] || 0);
                 calculatedCutLengthPrice = totalLength * hosePricePerMeter;
            }
            const protectionCostValue = params.selectedProtection === 'NOT REQUIRED' ? 0 : (parseFloat(params.protectionCost) || 0);
            const pressureTestCostValue = params.selectedPressure === 'Not Required' ? 0 : (parseFloat(params.pressureTestPrice) || 0);
            const subtotal = end1Cost + end2Cost + calculatedCutLengthPrice + protectionCostValue + pressureTestCostValue;
            const shipping = parseFloat(params.shippingCost) ?? (params.devModeActive ? 0 : 12.85);
            const subtotalWithShipping = subtotal + shipping;
            const gst = subtotalWithShipping * 0.1;
            const total = subtotalWithShipping + gst;
            prices = { subtotal, shipping, gst, total };
        }
    } catch (calcError) {
         console.error("[emailService > calculatePriceBreakdown] Error during calculation:", calcError);
         // Return default zeroed prices on error
    }
    return prices;
};

const formatPrice = (amount) => `A$${amount.toFixed(2)}`;

