// PayPalButtonWrapper - With dynamic script loading fallback
import React, { useEffect, useRef, useState } from 'react';
import { View, Platform, Text, ActivityIndicator, StyleSheet } from 'react-native';

const PayPalButtonWrapper = ({
    amount,
    currency = 'AUD',
    description = 'FluidPower Order',
    onApprove,
    onCancel,
    onError,
    disabled
}) => {
    const containerRef = useRef(null);
    const [isLoading, setIsLoading] = useState(false);
    const [sdkReady, setSdkReady] = useState(false);
    const scriptAddedRef = useRef(false);
    
    // First useEffect: Handle SDK loading with fallback
    useEffect(() => {
        // Only run on web platform
        if (Platform.OS !== 'web') return;
        
        console.log("PayPalButtonWrapper: Initializing PayPal integration");
        
        // Function to check if SDK is loaded
        const checkSDK = () => {
            if (window.paypal) {
                console.log("PayPalButtonWrapper: PayPal SDK detected");
                setSdkReady(true);
                return true;
            }
            return false;
        };
        
        // Function to dynamically add script if needed
        const addPayPalScript = () => {
            if (scriptAddedRef.current) return;
            
            console.log("PayPalButtonWrapper: Dynamically adding PayPal script");
            scriptAddedRef.current = true;
            
            const script = document.createElement('script');
            script.src = "https://www.paypal.com/sdk/js?client-id=AdYLJtivoqNzTAJ9-pho-cB5TcaSgg1MuOo_Hc-ENlSG9Upab9GQsO0Uip95jaGXL6vdCNyCT88erFnA&currency=AUD";
            script.async = true;
            
            script.onload = () => {
                console.log("PayPalButtonWrapper: Dynamically added script loaded");
                setSdkReady(true);
            };
            
            script.onerror = (err) => {
                console.error("PayPalButtonWrapper: Script load error:", err);
                if (onError) onError(new Error("PayPal SDK failed to load"));
            };
            
            document.head.appendChild(script);
        };
        
        // Check immediately if SDK is loaded from initial script tag
        if (checkSDK()) {
            return;
        }
        
        // Listen for the custom event from the index.html script
        const handleSDKReady = () => {
            console.log("PayPalButtonWrapper: PayPal SDK ready event detected");
            setSdkReady(true);
        };
        document.addEventListener('paypal-sdk-loaded', handleSDKReady);
        
        // Wait a short time, then check again before trying to add the script
        const initialTimeout = setTimeout(() => {
            if (!checkSDK()) {
                console.log("PayPalButtonWrapper: SDK not found after initial wait, adding script");
                addPayPalScript();
            }
        }, 1000);
        
        return () => {
            clearTimeout(initialTimeout);
            document.removeEventListener('paypal-sdk-loaded', handleSDKReady);
        };
    }, [onError]);
    
    // Second useEffect: Render PayPal buttons when SDK is ready
    useEffect(() => {
        // Only proceed if SDK is ready, we're on web, and have a valid container
        if (!sdkReady || Platform.OS !== 'web' || !containerRef.current || disabled) return;
        
        // Additional delay before rendering to ensure SDK is fully initialized
        const renderTimeout = setTimeout(() => {
            // Clear any previous content
            if (containerRef.current) {
                containerRef.current.innerHTML = '';
            }
            
            // Skip if amount is invalid
            if (isNaN(amount) || amount <= 0) {
                console.error('PayPalButtonWrapper: Invalid amount for PayPal', amount);
                if (onError) onError(new Error('Invalid amount'));
                return;
            }
            
            console.log("PayPalButtonWrapper: Starting PayPal button render");
            
            try {
                // Create the PayPal buttons
                window.paypal.Buttons({
                    // Style
                    style: {
                        layout: 'vertical',
                        color: 'gold',
                        shape: 'rect',
                        label: 'paypal',
                        height: 50
                    },
                    
                    // Create order
                    createOrder: function(data, actions) {
                        console.log('PayPalButtonWrapper: createOrder triggered');
                        setIsLoading(true);
                        
                        return actions.order.create({
                            purchase_units: [{
                                amount: {
                                    value: amount.toFixed(2),
                                    currency_code: currency
                                },
                                description: description
                            }],
                            application_context: {
                                shipping_preference: 'NO_SHIPPING',
                                user_action: 'PAY_NOW',
                            }
                        });
                    },
                    
                    // Payment approved
                    onApprove: function(data, actions) {
                        setIsLoading(false);
                        
                        if (onApprove) {
                            onApprove(data);
                        } else {
                            return actions.order.capture().then(function(details) {
                                console.log('PayPalButtonWrapper: Payment completed', details);
                            });
                        }
                    },
                    
                    // Payment cancelled
                    onCancel: function(data) {
                        console.log('PayPalButtonWrapper: Payment cancelled', data);
                        setIsLoading(false);
                        if (onCancel) onCancel(data);
                    },
                    
                    // Error occurred
                    onError: function(err) {
                        console.error('PayPalButtonWrapper: PayPal error', err);
                        setIsLoading(false);
                        if (onError) onError(err);
                    }
                }).render(containerRef.current).then(() => {
                    console.log("PayPalButtonWrapper: Render successful.");
                }).catch(err => {
                    console.error("PayPalButtonWrapper: Render failed:", err);
                    if (onError) onError(err);
                });
            } catch (error) {
                console.error('PayPalButtonWrapper: Error rendering PayPal buttons', error);
                if (onError) onError(error);
            }
        }, 500);
        
        return () => {
            clearTimeout(renderTimeout);
            if (containerRef.current) {
                containerRef.current.innerHTML = '';
            }
        };
    }, [sdkReady, amount, currency, description, onApprove, onCancel, onError, disabled]);
    
    // Render container
    return (
        <View style={styles.container}>
            {Platform.OS === 'web' ? (
                <>
                    {!sdkReady ? (
                        <View style={styles.loadingContainer}>
                            <ActivityIndicator size="small" color="#999" />
                            <Text style={styles.loadingText}>Loading PayPal...</Text>
                        </View>
                    ) : (
                        <div 
                            ref={containerRef}
                            style={styles.paypalContainer}
                        />
                    )}
                </>
            ) : (
                <Text style={styles.unsupportedText}>
                    PayPal is only available on web platforms
                </Text>
            )}
            {(disabled || isLoading) && sdkReady && (
                <View style={styles.disabledOverlay}>
                    <ActivityIndicator size="small" color="#999" />
                    <Text style={styles.disabledText}>Processing payment...</Text>
                </View>
            )}
        </View>
    );
};

const styles = StyleSheet.create({
    container: {
        width: '100%',
        minHeight: 50,
        position: 'relative'
    },
    paypalContainer: {
        width: '100%',
        minHeight: 50
    },
    loadingContainer: {
        width: '100%',
        minHeight: 50,
        justifyContent: 'center',
        alignItems: 'center',
        backgroundColor: '#f7f7f7',
        borderRadius: 4
    },
    loadingText: {
        marginTop: 8,
        color: '#666',
        fontSize: 14
    },
    unsupportedText: {
        textAlign: 'center',
        padding: 15,
        color: '#666'
    },
    disabledOverlay: {
        position: 'absolute',
        top: 0,
        left: 0,
        right: 0,
        bottom: 0,
        backgroundColor: 'rgba(255, 255, 255, 0.8)',
        justifyContent: 'center',
        alignItems: 'center',
        zIndex: 1
    },
    disabledText: {
        marginTop: 10,
        color: '#666',
        fontSize: 14
    }
});

export default PayPalButtonWrapper;