import React, { useState, useRef, useEffect, useCallback } from 'react';
import {
  View, Text, StyleSheet, SafeAreaView, Image, ScrollView,
  Animated, TouchableOpacity, Platform, ActivityIndicator, Alert
} from 'react-native';
import { useNavigation, useRoute } from '@react-navigation/native';
import ScrollableGradientWrapper from '../components/ScrollableGradientWrapper';
import PayPalButtonWrapper from '../components/PayPalButtonWrapper';
import Svg, { Path } from 'react-native-svg';

const PaymentScreen = () => {
  const navigation = useNavigation();
  const route = useRoute();
  const fadeAnim = useRef(new Animated.Value(0)).current;
  
  // Simple payment status
  const [paymentStatus, setPaymentStatus] = useState('idle'); // 'idle', 'processing', 'success', 'cancelled', 'error'
  const [errorMessage, setErrorMessage] = useState('');
  // --- Extract route parameters ---
  const {
    selectedHose,
    selectedHosePrice,
    selectedFitting,
    end1Shape,
    end1Size,
    end1Price,
    selectedEnd2Fitting,
    end2Shape,
    end2Size,
    end2Price,
    selectedAngle,
    quantity,
    cutLengths,
    cutLengthPrice,
    isDevMode: devModeActive,
    isOrderFittingMode,
    selectedProtection,
    hoseProtectionPrice,
    selectedPressure,
    pressureTestPrice,
    selectedMethod = 'Standard',
    selectedService = 'Standard Shipping',
    shippingCost,
    totalBeforeShipping,
    totalPriceWithShipping,
    protectionCost,
    shippingDetails
  } = route.params || {};

  // --- Helper Functions ---
  const cleanPriceString = (priceStr) => {
    if (!priceStr) return 0;
    return parseFloat(priceStr.replace(/\s+/g, '').split('/')[0]);
  };

  // Calculation function that preserves fittings only mode
  const calculateTotalBeforeShipping = useCallback(() => {
    // If we're in isOrderFittingMode, use the cutLengthPrice directly
    if (isOrderFittingMode) {
      return cutLengthPrice || 0;
    }

    // For normal hose orders
    const end1Cost = typeof end1Price === 'number' ? end1Price : parseFloat(end1Price) || 0;
    const end2Cost = typeof end2Price === 'number' ? end2Price : parseFloat(end2Price) || 0;

    // Get base hose price from cut lengths
    let cutLengthCost = 0;
    if (cutLengths && cutLengths.length > 0) {
      const totalLength = cutLengths.reduce((total, cut) => {
        if (!cut.length) return total;
        return total + (parseFloat(cut.length) / 1000); // Convert mm to meters
      }, 0);
      
      cutLengthCost = totalLength * cleanPriceString(selectedHosePrice);
    } else {
      cutLengthCost = cutLengthPrice || 0;
    }

    // Get protection cost
    const protectionAmount = selectedProtection === 'NOT REQUIRED' ? 0 : (protectionCost || 0);

    // Get pressure test cost
    const pressureTestAmount = selectedPressure === 'Not Required' ? 0 : (pressureTestPrice || 0);

    // Sum all components
    return end1Cost + end2Cost + cutLengthCost + protectionAmount + pressureTestAmount;
  }, [isOrderFittingMode, cutLengthPrice, end1Price, end2Price, cutLengths, selectedHosePrice, selectedProtection, protectionCost, selectedPressure, pressureTestPrice]);

  // Price calculations
  const calculatedSubtotal = calculateTotalBeforeShipping();
  const subtotal = isOrderFittingMode ? cutLengthPrice : (totalBeforeShipping || calculatedSubtotal);
  const shippingAmount = devModeActive ? 0 : (shippingCost || 12.85);
  const total = subtotal + shippingAmount;
  const gst = total * 0.10;
  const finalTotal = devModeActive ? 0.20 : (total + gst);

  useEffect(() => {
    Animated.timing(fadeAnim, {
      toValue: 1,
      duration: 800,
      useNativeDriver: Platform.OS !== 'web',
    }).start();
  }, [fadeAnim]);

  // --- PayPal Event Handlers ---
  const handlePayPalApprove = useCallback(async (sdkData) => { 
    
    if (!sdkData || !sdkData.orderID) {
      console.error("PaymentScreen: Approve callback missing orderID!");
      setPaymentStatus('error');
      setErrorMessage('Critical error after PayPal approval. Missing Order ID.');
      return;
    }
    
    setPaymentStatus('processing');
    
    // Create an internal order number
    const internalOrderNumber = `FPG-${Date.now()}`;
    
    try {
      // Option 1: Quick demo approach - just use the PayPal order ID without server-side capture
     /* setTimeout(() => {
        console.log("PaymentScreen: Demo payment processed successfully");
        navigation.replace('OrderConfirmation', {
          success: true,
          orderDetails: {
            orderID: sdkData.orderID,
            orderNumber: internalOrderNumber,
            totalAmount: finalTotal,
            userDetails: shippingDetails
          }
        });
      }, 1500); */
      
      
      // Option 2: Server-side capture (This can be uncommented when your backend is fully ready)
      const emailTemplatesContent = {
        customerEmailContent: `<p>Thank you for your order, ${route.params?.shippingDetails?.name || 'Customer'}!</p><p>Your Order Number: ${internalOrderNumber}</p><p>Amount: A$${finalTotal.toFixed(2)}</p>`,
        businessEmailContent: `<p>New order ${internalOrderNumber}.</p><p>Customer: ${route.params?.shippingDetails?.name || 'N/A'}</p><p>Email: ${route.params?.shippingDetails?.email || 'N/A'}</p><p>Amount: A$${finalTotal.toFixed(2)}</p>`
      };
  
      const payloadForBackend = {
        orderID: sdkData.orderID,
        payerID: sdkData.payerID,
        orderNumber: internalOrderNumber,
        userDetails: route.params?.shippingDetails,
        emailTemplates: emailTemplatesContent,
        originalOrderParams: route.params
      };
      
      console.log("PaymentScreen: Payload successfully prepared for backend capture");
  
      const response = await fetch(`${process.env.REACT_APP_API_URL}/api/paypal/capture-order`, {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify(payloadForBackend)
      });
      
      if (!response.ok) {
        const errorText = await response.text();
        let errorJson;
        try {
          errorJson = JSON.parse(errorText);
        } catch (e) {
          // If parsing fails, use the raw text
        }
        
        const errorMessage = errorJson?.error || `Server error: ${response.status}`;
        throw new Error(errorMessage);
      }
      
      const captureResult = await response.json();
  
      if (captureResult.success) {
        console.log("PaymentScreen: Server-side capture successful!");
        const transactionId = captureResult.paypalCaptureID;
        navigation.replace('OrderConfirmation', {
          success: true,
          orderDetails: payloadForBackend, transactionId 
        });
      } else {
        console.error("PaymentScreen: Server-side capture failed:", captureResult.error);
        setPaymentStatus('error');
        setErrorMessage(captureResult.error || 'An error occurred processing your payment after approval.');
      }
      
      
    } catch (error) {
      console.error("PaymentScreen: Error in payment process:", error);
      setPaymentStatus('error');
      setErrorMessage(error.message || 'A network error occurred while finalizing your payment.');
    }
  }, [navigation, finalTotal, shippingDetails]);



  const handlePayPalCancel = useCallback((data) => {
    console.log('Payment cancelled:', data);
    setPaymentStatus('cancelled');
  }, []);

  const handlePayPalError = useCallback((error) => {
    console.error('Payment error:', error);
    setPaymentStatus('error');
    setErrorMessage(error.message || 'An error occurred during payment');
    
    Alert.alert(
      'Payment Error',
      'There was a problem with your payment. Please try again.'
    );
  }, []);

  const handleRetryPayment = useCallback(() => {
    setPaymentStatus('idle');
    setErrorMessage('');
  }, []);

  const handleStartNewOrder = useCallback(() => {
    navigation.reset({
      index: 0,
      routes: [{ name: 'Welcome' }]
    });
  }, [navigation]);

  // --- Clean up on unmount ---
  useEffect(() => {
    return () => {
      // Reset any global state or cleanup
      //console.log("PaymentScreen unmounting - performing cleanup");
    };
  }, []);

  // --- UI Components ---
  const renderShippingDetails = () => (
    <View style={styles.shippingDetailsContainer}>
      <View style={styles.sectionTitleContainer}>
        <Text style={styles.sectionTitleText}>Shipping Details</Text>
      </View>
      <View style={styles.detailsGrid}>
        {shippingDetails?.name && (
          <View style={styles.detailRow}>
            <Text style={styles.detailLabel}>Name:</Text>
            <Text style={styles.detailValue}>{shippingDetails.name}</Text>
          </View>
        )}
        {shippingDetails?.companyName && (
          <View style={styles.detailRow}>
            <Text style={styles.detailLabel}>Company:</Text>
            <Text style={styles.detailValue}>{shippingDetails.companyName}</Text>
          </View>
        )}
        {shippingDetails?.address && (
          <View style={styles.detailRow}>
            <Text style={styles.detailLabel}>Address:</Text>
            <Text style={styles.detailValue}>{shippingDetails.address}</Text>
          </View>
        )}
        {shippingDetails?.suburb && (
          <View style={styles.detailRow}>
            <Text style={styles.detailLabel}>Suburb:</Text>
            <Text style={styles.detailValue}>{shippingDetails.suburb}</Text>
          </View>
        )}
        {shippingDetails?.state && (
          <View style={styles.detailRow}>
            <Text style={styles.detailLabel}>State:</Text>
            <Text style={styles.detailValue}>{shippingDetails.state}</Text>
          </View>
        )}
        {shippingDetails?.postcode && (
          <View style={styles.detailRow}>
            <Text style={styles.detailLabel}>Postcode:</Text>
            <Text style={styles.detailValue}>{shippingDetails.postcode}</Text>
          </View>
        )}
        {shippingDetails?.email && (
          <View style={styles.detailRow}>
            <Text style={styles.detailLabel}>Email:</Text>
            <Text style={styles.detailValue}>{shippingDetails.email}</Text>
          </View>
        )}
        {shippingDetails?.contactNumber && (
          <View style={styles.detailRow}>
            <Text style={styles.detailLabel}>Contact:</Text>
            <Text style={styles.detailValue}>{shippingDetails.contactNumber}</Text>
          </View>
        )}
      </View>
    </View>
  );

  const renderPriceBreakdown = () => (
    <View style={styles.priceBreakdownContainer}>
      <View style={styles.sectionTitleContainer}>
        <Text style={styles.sectionTitleText}>Price Breakdown</Text>
      </View>
      <View style={styles.priceDetailsGrid}>
        <View style={styles.priceRow}>
          <Text style={styles.priceLabel}>Subtotal:</Text>
          <Text style={styles.priceValue}>A${subtotal.toFixed(2)}</Text>
        </View>
        <View style={styles.priceRow}>
          <Text style={styles.priceLabel}>Shipping Cost:</Text>
          <Text style={styles.priceValue}>
            {shippingAmount > 0 ? `A$${shippingAmount.toFixed(2)}` : 'FREE'}
          </Text>
        </View>
        <View style={styles.priceRow}>
          <Text style={styles.priceLabel}>GST (10%):</Text>
          <Text style={styles.priceValue}>A${gst.toFixed(2)}</Text>
        </View>
      </View>
    </View>
  );

  const renderStatusMessage = () => {
    if (paymentStatus === 'cancelled') {
      return (
        <View style={[styles.statusContainer, { backgroundColor: '#fff3cd' }]}>
          <Text style={styles.statusTitle}>Payment Cancelled</Text>
          <Text style={styles.statusMessage}>
            Your payment was cancelled. No charges have been applied.
          </Text>
          <TouchableOpacity style={styles.retryButton} onPress={handleRetryPayment}>
            <Text style={styles.buttonText}>Try Again</Text>
          </TouchableOpacity>
          <TouchableOpacity style={styles.newOrderButton} onPress={handleStartNewOrder}>
            <Text style={styles.buttonText}>Start New Order</Text>
          </TouchableOpacity>
        </View>
      );
    }
    
    if (paymentStatus === 'error') {
      return (
        <View style={[styles.statusContainer, { backgroundColor: '#f8d7da' }]}>
          <Text style={styles.statusTitle}>Payment Error</Text>
          <Text style={styles.statusMessage}>
            {errorMessage || 'There was a problem processing your payment.'}
          </Text>
          <TouchableOpacity style={styles.retryButton} onPress={handleRetryPayment}>
            <Text style={styles.buttonText}>Try Again</Text>
          </TouchableOpacity>
          <TouchableOpacity style={styles.newOrderButton} onPress={handleStartNewOrder}>
            <Text style={styles.buttonText}>Start New Order</Text>
          </TouchableOpacity>
        </View>
      );
    }
    
    return null;
  };

  // --- Main Render ---
  return (
    <View style={styles.mainContainer}>
      {/* Floating Back Button */}
      <TouchableOpacity 
          style={styles.floatingBackButton}
          onPress={() => navigation.goBack()}
        >
          <Svg width="24" height="24" viewBox="0 0 24 24" fill="none">
          <Path 
            d="M19 12H5M12 19L5 12L12 5" 
            stroke="#4a4a4a" 
            strokeWidth="2.5" 
            strokeLinecap="round" 
            strokeLinejoin="round"
          />
        </Svg>
        </TouchableOpacity>
      
      <ScrollableGradientWrapper>
        <SafeAreaView style={styles.container}>
          {/* Logo */}
          <View style={styles.logoContainer}>
            <Image 
              source={require('../assets/fluidpower_logo.gif')} 
              style={styles.logo} 
              resizeMode="contain"
            />
          </View>
          
          <Animated.View style={[styles.contentContainer, { opacity: fadeAnim }]}>
            <ScrollView 
              contentContainerStyle={styles.scrollContent}
              showsVerticalScrollIndicator={true}
            >
              {/* Payment details title 
              <View style={styles.pageTitle}>
                <Text style={styles.pageTitleText}>Payment Details</Text>
              </View>*/}
              
              <View style={styles.formContainer}>
                {/* Shipping details */}
                {renderShippingDetails()}
                
                {/* Price summary (simplified) 
                <View style={styles.sectionContainer}>
                  <View style={styles.sectionHeader}>
                    <Text style={styles.sectionTitle}>Price Summary</Text>
                  </View>
                  <View style={styles.detailsGrid}>
                    <View style={styles.detailRow}>
                      <Text style={styles.detailLabel}>Total Amount:</Text>
                      <Text style={styles.detailValue}>A${finalTotal.toFixed(2)}</Text>
                    </View>
                  </View>
                </View>*/}

                {renderPriceBreakdown()}
                
                {/* Total amount */}
                <View style={styles.totalContainer}>
                  <Text style={styles.totalText}>
                    {devModeActive ? 'Dev Mode Amount: ' : 'Total Amount: '}
                    A${finalTotal.toFixed(2)}
                  </Text>
                </View>
                
                {/* PayPal payment */}
                <View style={styles.paymentContainer}>
                  {paymentStatus === 'idle' ? (
                    <>
                      <PayPalButtonWrapper 
                        amount={finalTotal}
                        description={`FluidPower Order - Total A$${finalTotal.toFixed(2)}`}
                        onApprove={handlePayPalApprove}
                        onCancel={handlePayPalCancel}
                        onError={handlePayPalError}
                        disabled={paymentStatus === 'processing'}
                      />
                      <Text style={styles.paymentNote}>
                        Secure payment processed by PayPal. You don't need a PayPal account to pay.
                      </Text>
                    </>
                  ) : (
                    renderStatusMessage()
                  )}
                </View>
              </View>
            </ScrollView>
          </Animated.View>
        </SafeAreaView>
      </ScrollableGradientWrapper>
      
      {/* Processing overlay */}
      {paymentStatus === 'processing' && (
        <View style={styles.overlay}>
          <ActivityIndicator size="large" color="#4a4a4a" />
          <Text style={styles.overlayText}>Processing your payment...</Text>
        </View>
      )}
    </View>
  );
};

